	<template>
		<div>
			<ul>
				<li v-for="key in arr" @click = "methodClick(key)">{{key}}</li>
			</ul>
		</div>
		
	</template>

	<script type="text/javascript">
	 import router from '../router'
	export default {
		data(){
			return {
				arr:["111","2222","3333","444"]
			}
		},
		methods:{
			methodClick(key){
				router.push(`details/${key}`)
			}
		},
		mounted(){
			console.log(this.arr)
		}
	}
	</script>	

	<style type="text/css" media="screen" scoped>
		div{
			color:blue;
		}
	</style>


