import Vue from 'vue'
import Router from 'vue-router'
import home from '../components/home'
import film from '../components/film'
import card from '../components/card'
import details from '../components/details'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: home
    },
    {
      path: '/film',
      component: film,

      children:[
      {
          path: 'details/:id',
          component: details,
      }
      ]
    },
    {
      path: '/card',
      component: card
    },
    {
      path:"/details/:id",
      component:details
    }
  ]
})



